@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.8rem;
  --typing-font:3rem;
}

* {
  margin:0;
  padding: 0;
}

body {
  font-family: 'Raleway';
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

/* .condiv class is for the right side content container*/
.condiv {
  padding: 100px ;
  background-color: #d2d2f0;
  flex:var(--condiv-flex);
}


nav {
  padding:250px 30px 0px 30px ;
  flex:var(--sidebar-flex);
  background: rgb(59, 54, 65);
  background: linear-gradient(180deg, rgb(45, 39, 71) 50%, rgb(56, 53, 104) 80%);
}

nav ul {
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: rgb(240, 250, 229) !important;
  margin-left: 40px;
  position: fixed;
}

nav ul li {
  margin:40px;
}

a {
  text-decoration: none;
  color: white;
}

.subtopic {
  margin:10px 10px 30px 10px !important;
}

p {
  font-size: var(--fontsize-p);
}

h1,h2,h3,h4,p {
  margin: 10px;
}

.active {
  font-weight: bolder;
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
}
.profilepic {
  border-radius: 20%;
  width: 200px;
  margin: 30px;
}

.typingeffect {
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}

.social i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}

.social i:hover {
  color: #ffffff;
}

.widecard {
  border:0px solid #9a9b9c;
  background-color: #b9b9e9;
  border-radius: 25px;
  display: flex;
  margin:30px 0px 30px 0px;
}

.widecard:hover {
  color: #1b1d20;
  background-color: #b0b0c2;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.project_image {
  border-radius: 20px;
  width: 450px;
  height: 250px;
  margin: 20px;

}

.project_image_phone {
  border-radius: 20px;
  width: 200px;
  height: 300px;
  margin: 20px;

}

a.repo_link {
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.05em solid #20379e;
  border-radius: 20px;
  margin:0 0.3em 0.3em 1em;
  box-sizing: border-box;
  background-color: #20379e;
  font-weight:500;
  color:#FFFFFF;
  text-align:center;
  transition: all 0.2s;
}

a.repo_link:hover{
  color:#000000;
  background-color:#FFFFFF;
  }

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }

  .App {
    flex-direction: column;
  }

  .sidebar {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }

  .condiv {
    padding: 50px ;
  }

  nav ul {
    display: flex;
  }

  nav ul li {
    margin: 40px;
  }

  .social {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }

  .social i {
    padding-right: 0px;
  }

  
  a.repo_link{
    display:block;
    margin:0.4em auto;
    }
}